import { Component, OnInit } from '@angular/core';
import { RestApiService } from 'src/app/services/rest-api.service';
import { AuthenticateService } from 'src/app/services/authentication.service';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-public-user',
  templateUrl: './public-user.page.html',
  styleUrls: ['./public-user.page.scss'],
})
export class PublicUserPage implements OnInit {

  objs:Array<any> = [];

  constructor(public restApi: RestApiService, public authService: AuthenticateService, public toastController: ToastController) { }

  ngOnInit() {
    this.getProfess();
  }

  toast(message) {
    this.toastController.create({
      message: message,
      duration: 2000
    }).then(toast => toast.present());
  }

  getProfess() {
    this.restApi.post('professional/get-profess', { user_id: this.authService.user.userId }).subscribe((res: any) => {
      if (res && res.status) {
        if (res.status == 'success') {
          this.objs = res.data;
          console.log(this.objs);
        } else {
          this.toastController.create({
            message: res.message,
            duration: 2000
          }).then(toast => toast.present());
        }
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }
}
